<template>
   <div>
       <!--    评论页面-->
      <van-cell title="发表评论" class="vantitle"/>
      <van-field class="vanfield" v-model="content" rows="4" type="textarea" size="large" placeholder="请输入留言"/>
        <van-button type="info" @click="recomment" size="large" class="recommnetbtn"> 发表评论</van-button>
      <!--    评论详细信息-->
      <van-cell-group :title="'第'+(index*1+1)+'楼 '+'用户：'+item.user_name"
                      v-for="(item,index) in commentList"
                      :key="item.index" class="vancgroup">
        <span class="addtime">发表时间：{{item.add_time|dateFormat}}</span>
        <van-cell :title="item.content"/>
      </van-cell-group>
      <!--加载更多-->
      <van-button plain type="info" @click="getMoreCommentList" size="large" class="onload">加载更多
      </van-button>
   </div>
</template>
<script>
export default {
  name: 'GComment',
  data () {
    return {
      // 评论列表
      commentList: [],
      isLoading: false,
      page: 1,
      // 绑定输入的内容
      content: ''
    }
  },
  created () {
    this.getCommentList()
  },
  methods: {
    // 获取评论列表
    async getCommentList () {
      const res = await this.$http.get('/api/getcomments/' + this.$route.params.id + '?pageindex=' + this.page)
      if (res.status !== 0) {
        return this.$toast.fail('获取失败')
      }
      this.commentList.push(...res.message)
      console.log(this.commentList)
    },
    // 加载更多
    async getMoreCommentList () {
      this.page++
      this.getCommentList()
    },
    // 提交评论
    async recomment () {
      // 评论域验证
      if (this.content.trim().length === 0) return this.$toast.fail('请输入内容')
      this.page = 1
      this.commentList = []
      const res = await this.$http.post('/api/postcomment/' + this.$route.params.id, { content: this.content })
      if (res.status !== 0) return this.$toast.fail('评论失败')
      this.getCommentList()
      this.content = ''
    }
  }
}
</script>
<style lang="less" scoped>
 .van-cell-group__title {
    color: black !important;
    background-color: #ccc !important;
    font-size: 12px;
  }
   .recommnetbtn {
    margin-top: 10px;
  }
.vantitle {
    font-weight: bold;
    font-size: 16px;
  }
  .vancgroup{
    position: relative;
  }
   .addtime{
    position: absolute;
    font-size: 12px;
   margin-top: -24px;
   margin-left: 150px;
  }
  .onload {
    margin: 10px;
  }
  .vanfield {
    border: 1px solid #ccc;
    margin-top: 10px

  }
</style>
