<template>
  <div>
    <!--    标题内容-->
   <header><g-header></g-header></header>
    <div class="large">
      <!--    内容详情页头部-->
      <van-panel>
        <p class="vanp">{{detailNewsList.length&&detailNewsList[0].title}}</p>
        <div class="vand">
          <span>发表时间：{{detailNewsList.length&&detailNewsList[0].add_time|dateFormat}}</span>
          <span>点击次数：{{detailNewsList.length&&detailNewsList[0].click}}</span>
        </div>
        <van-divider :style="{ color: '#ccc', borderColor: '#ccc', padding: '0 16px' }"/>
        <div class="vpdiv" v-html="detailNewsList.length&&detailNewsList[0].content">
          {{detailNewsList.length&&detailNewsList[0].content}}
        </div>
      </van-panel>
      <!-- 评论组件 -->
     <g-comment></g-comment>
    </div>
    <!--    底部-->
     <div id="tabbar">
      <g-tabbar></g-tabbar>
      </div>
  </div>
</template>
<script>
import GHeader from '../header/index'
import GTabbar from '../tabbar/index'
import GComment from '../comments/index'
export default {
  data () {
    return {
      active: 0,
      detailNewsList: []

    }
  },
  created () {
    this.getDetailNews()
    // this.getCommentList()
  },
  methods: {
    //  获取文章详情内容
    async getDetailNews () {
      const res = await this.$http.get('/api/getnew/' + this.$route.params.id)
      if (res.status !== 0) return this.$toast.fail('列表获取失败')
      this.detailNewsList = res.message
      // console.log(this.detailNewsList)
    }
    // //返回页面
    // onClickLeft() {
    //   this.$router.push('/news')
    // },

  },
  components: {
    GHeader,
    GTabbar,
    GComment
  }
}
</script>
<style lang="less" scoped>
  .van-nav-bar__text, .van-nav-bar__arrow:before {
    color: #fff
  }
  .van-tabs__line {
    width: 0px !important;
  }
  .van-panel__header::after {
    content: none !important;
  }
  .van-cell {
    padding-top: 0 !important;;
  }
  .vanp {
    display: flex;
    font-size: 14px;
    font-weight: bold;
    color: #1998FB;
    margin-top: 0px;
    justify-content: center;

  }
  .vand {
    display: flex;
    justify-content: space-between;
    color: #1998FB;
    font-size: 12px;
     margin-bottom: 0px;
  }

  .large {
    padding: 0 16px;
  }

</style>
