<template>
  <div>
    <!--    标题内容-->
    <header><g-header></g-header></header>

    <!--    下拉刷新-->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <!--    商品内容-->

    <van-card  class="vancard" :title="item.title" :thumb="item.img_url" v-for="item in newsList" :key="item.id"
              @click="newsDetial(item.id)" >
      <template #desc>
        <div class="newscard">
          <span>发表时间：{{item.add_time|dateFormat}}</span>
          <span>点击：{{item.click}}</span>
        </div>
      </template>
    </van-card>
    </van-pull-refresh>
    <!-- 尾部 -->
    <div id="tabbar">
      <g-tabbar></g-tabbar>
      </div>
  </div>
</template>
<script>
import '@vant/touch-emulator'
import GHeader from '../header/index'
import GTabbar from '../tabbar/index'
export default {

  data () {
    return {
      newsList: [],
      isLoading: true

    }
  },
  created () {
    this.getNewsList()
  },
  methods: {
    // 调用后台新闻详情页
    async  getNewsList () {
      const res = await this.$http.get('/api/getnewslist')
      if (res.status !== 0) return this.$toast.fail('获取数据失败')
      this.newsList = res.message
      console.log(this.newsList)
    },
    newsDetial (id) {
      console.log(id)
      this.$router.push('/api/getnew/' + id)
    },
    onClickLeft () {
      this.$router.push('/home')
    },
    // 下拉刷新
    onRefresh () {
      // setTimeout(() => {
      //
      //   this.isLoading = false;

      // }, 1000);
      this.getNewsList()
      this.$toast.success('刷新成功')
      this.isLoading = false
    }
  },
  components: {
    GHeader,
    GTabbar
  }
}
</script>
<style lang="less" scoped>
  .van-nav-bar__text, .van-nav-bar__arrow:before {
    color: #fff
  }

  .newscard {
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    color: #0000cc;
  }
  .van-card {
    height: 68px;
  }
  .van-card__title {
    font-weight: bold;
    font-size: 14px;
  }
  .van-image {
    width: 50% !important;
    height: 50% !important;
    margin-right: -50px !important;
  }
  .van-card__thumb {
    margin-right: -40px;
  }
  header {
  width: 100%;
}
</style>
